import { Space, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    EyeOutlined

} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const subHospitalCol = ({handleDelete,hospitalName}) => {
    var array = [
        {
            title: 'ល.រ',
            dataIndex: 'id',
            key: 'id',
            width: 20,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: 'ឈ្មោះ',
            dataIndex: 'name',
            key: 'name',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.lastName} {record?.firstName}
                </span>
            ),
        },
        {
            title: 'ភេទ',
            dataIndex: 'gender',
            key: 'gender',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.gender}
                </span>
            ),
        },
        {
            title: 'អាយុ',
            dataIndex: 'age',
            key: 'age',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.age}
                </span>
            ),
        },
        {
            title: 'លេខទូរស័ព្ទ',
            dataIndex: 'phone',
            key: 'phone',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.tel}
                </span>
            ),
        },

        {
            title: 'កាលបរិច្ឆេទចូល',
            dataIndex: 'date_in',
            key: 'date_in',
            width: 30,
            render: (text, record) => {
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]
                return(
                <Space size="middle">
                    {hospitalization.date_in === null ? "គ្មាន": moment.parseZone(hospitalization.date_in).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                    {/* {moment.parseZone(hospitalization?.date_in).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")} */}
                </Space>
            )
            }
        },

        {
            title: 'កាលបរិច្ឆេទចេញ',
            dataIndex: 'date_out',
            key: 'date_out',
            width: 30,
            render: (text, record) => {

                // let hospitalization = record?.hospitalizations?.filter(e=> e.hospitalName === hospitalName  )
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]

                return(
                <span size="middle">
                    {hospitalization.date_out === null ? "គ្មាន": moment.parseZone(hospitalization.date_out).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                    {/* {
                        console.log(hospitalization,hospitalName,"test")
                    } */}
                </span>
                )
            },
        },
      
        {
            title: 'អាសយដ្ឋាន',
            dataIndex: 'address',
            key: 'address',
            width: 70,
            render: (text, record) => (
                <Space size="middle">
                    {(record?.village !== "ក្រៅសៀមរាប") && record?.village}
                    {record?.commune !== "ក្រៅសៀមរាប" && record?.commune}
                    {record?.district !== "ក្រៅសៀមរាប" && record?.district}
                    {record?.province && record?.province}
                </Space>
            ),
        },
        {
            title: 'ស្ថានភាព',
            dataIndex: 'status',
            key: 'status',
            width: 50,
            render: (text, record) => {

                let hospitalLength = record?.hospitalizations?.length
                let arr = record?.hospitalizations[hospitalLength-1]

                var dayOut = moment.parseZone(arr?.date_out).format("DD MM YYYY");
                var dayNow = moment.parseZone(new Date()).format("DD MM YYYY");
                let state ="កុំពុងព្យាបាល";
                let classN = "badge-purple"
                if(arr.date_in !== null && dayOut > dayNow){
                    state = "កុំពុងព្យាបាល"
                    classN= "badge-purple"
                }
                if(arr.date_out!==null &&  dayOut < dayNow){
                    state = "ចេញ​ពីព្យាបាលហើយ"
                    classN= "badge-success"
                }

                return (
                    <span className={classN}>{state}</span>
                )
            }
                // <Space size="middle">
                //     {record?.personalInfo?.currentState?.confirm ? "វិជ្ជមាន" : "អវិជ្ជមាន"}
                // </Space>
         
        },
        // {
        //     title: 'លក្ខណៈពាក់ព័ន្ធ',
        //     dataIndex: 'relatedInfo',
        //     key: 'relatedInfo',
        //     width: 30,
        //     render: (text, record) => (
        //         <Space size="middle">
        //             {record?.personalInfo?.direct ? "ផ្ទាល់":"ប្រយោល"}
        //         </Space>
        //     ),
        // },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record?.id}><EyeOutlined /></Link>
                    {/* <Popconfirm
                        title="តើអ្នកពិតចង់លុបមែនឬទេ?"
                        onConfirm={() => {handleDelete(record.id) }}
                        okText="ចង់"
                        cancelText="មិនចង់"
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm> */}

                </Space>
            ),
        }
    ]
    return array
}