import React,{useState} from 'react';
import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const sampleCol = ({handleSampleTestDelete,limit,page}) => {
    // let no = ((page-1) * limit)
    
    let no = (page * limit) - limit
    
    var array = [
        {
            title: 'ល.រ',
            dataIndex: 'no',
            key: 'no',
            width: 30,
            // render: (text, record) => {
                // if(no/limit === 1){
                //     no = 0
                // }
               
            // return(
            //     <>
            //         {no+=1}
            //     </>
            // )},
        },
        {
            title: 'អត្តសញ្ញាណប័ណ្ណ',
            dataIndex: 'idCard',
            key: 'idCard',
            width: 100,
        },
        {
            title: 'លេខសម្គល់អ្នកជំងឺ',
            dataIndex: 'patientId',
            key: 'patientId',
            width: 100,
        },
        {
            title: 'ឈ្មោះ',
            dataIndex: 'name',
            key: 'name',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.lastName+" "+record?.firstName}
                </span>
            ),
        },
        {
            title: 'ភេទ',
            dataIndex: 'genderId',
            key: 'genderId',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.gender}
                </span>
            ),
        },
        {
            title: 'អាយុ',
            dataIndex: 'ageId',
            key: 'ageId',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.age}
                </span>
            ),
        },
        {
            title: 'លេខទូរស័ព្ទ',
            dataIndex: 'phoneId',
            key: 'phoneId',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.tel}
                </span>
            ),
        },
        {
            title: 'ចំនួនយកសំណាក',
            dataIndex: 'times',
            key: 'times',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.sampleTest?.length} លើក
                </span>
            ),
        },
        {
            title: 'ស្ថានភាព',
            dataIndex: 'result',
            key: 'result',
            width: 100,
            render: (text, record) => {
                let state ="អវិជ្ជមាន";
                let classN = "badge-success"
                let c_state = record?.currentState;

                if(c_state.confirm===true){
                    state = "វិជ្ជមាន"
                    classN= "badge-purple"
                }

                if(c_state.recovered===true){
                    state = "ជាសះស្បើយ"
                    classN= "badge-lightgreen"
                }

                if(c_state.death===true){
                    state = "ស្លាប់"
                    classN= "badge-error"
                }

                return (
                    <span className={classN}>{state}</span>
                )
            }
        },
        {
            title: 'កាលបរិច្ឆេទវិជ្ជមាន',
            dataIndex: 'confirmedAt',
            key: 'confirmedAt',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.currentState?.confirmedAt === null ? "" : moment.parseZone(record?.currentState?.confirmedAt).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                </span>
            ),
        },
        {
            title: 'កាលបរិច្ឆេទស្លាប់',
            dataIndex: 'deathAt',
            key: 'deathAt',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.currentState?.deathAt === null ? "" : moment.parseZone(record?.currentState?.deathAt).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                </span>
            ),
        },
      
      
       
        // {
        //     title: 'អាការ',
        //     dataIndex: 'symptom',
        //     key: 'symptom',
        //     width: 50,
        // },
        // {
        //     title: 'ផ្សេងៗ',
        //     dataIndex: 'other',
        //     key: 'other',
        //     width: 100,
        // },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    {/* <Popconfirm
                        title="តើអ្នកពិតចង់លុបមែនឬទេ?"
                        onConfirm={() => { handleSampleTestDelete(record.id) }}
                        okText="ចង់"
                        cancelText="មិនចង់"
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm> */}

                </Space>
            ),
        }
    ]

    return array
}