import { Space, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    EyeOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const subQuarantineCol = ({handleDelete, handleEditSubQuarantine,limit,page}) => {
    let no = (page-1) * limit
    var array = [
        {
            title: 'ល.រ',
            dataIndex: 'id',
            key: 'id',
            width: 20,
            render: (text, record, i) => (
                <span size="middle">
                    {no+=1}
                </span>
            ),
            
        },
        {
            title: 'ឈ្មោះ',
            dataIndex: 'quarantines',
            key: 'quarantines',
            width: 70,
            render: (text, record) => (
                
                <span size="middle">
                    {record?.lastName} {record?.firstName}
                </span>
            ),
        },
        {
            title: 'ភេទ',
            dataIndex: 'gender',
            key: 'gender',
            width: 50,
            render: (text, record) => (
               
                <span size="middle">
                    {record?.gender} 
                </span>
            ),
        },
        {
            title: 'អាយុ',
            dataIndex: 'age',
            key: 'age',
            width: 50,
            render: (text, record) => (
                
                <span size="middle">
                    {record?.age} 
                </span>
            ),
        },
        {
            title: 'លេខទូរស័ព្ទ',
            dataIndex: 'phone',
            key: 'phone',
            width: 70,
            render: (text, record) => (
                
                <span size="middle">
                    {record?.tel} 
                </span>
            ),
        },
        {
            title: 'ស្ថានភាព',
            dataIndex: 'status',
            key: 'status',
            width: 100,
            render: (text, record) => {
                let state ="កុំពុងធ្វើចត្តាឡីស័ក";
                let classN = "badge-purple"
                var length = record?.quaranting.length;
                var arr = record?.quaranting[length-1];
                var dayOut = moment.parseZone(arr?.date_out).format("DD MM YYYY");
                var dayNow = moment.parseZone(new Date()).format("DD MM YYYY");
                // console.log(nowday,"see date new date");
                // console.log(record);
                if(arr.date_in !== null &&  dayOut < dayNow){
                    state ="ចេញពីធ្វើចត្តាឡីស័ក";
                    classN= "badge-success";
                }
                if(arr.date_out !== null && dayOut > dayNow ){
                    state ="កុំពុងធ្វើចត្តាឡីស័ក";
                    classN= "badge-purple"
                }
                return(
                <Space size="middle">
                    {/* {record?.currentState?.confirm ? "វិជ្ជមាន" : "អវិជ្ជមាន"} */}
                    <span className={classN}>{state}</span>
                </Space>
                )
            },
        },
        {
            title: 'កាលបរិច្ឆេទចូល',
            dataIndex: 'date_in',
            key: 'date_in',
            width: 70,
            render: (text, record) => {
                var length = record?.quaranting.length;
                var arr = record?.quaranting[length-1];
            return(
                <Space size="middle">
                    {moment.parseZone(arr?.date_in).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                </Space>
                )
            }
        },

        {
            title: 'កាលបរិច្ឆេទចេញ',
            dataIndex: 'date_out',
            key: 'date_out',
            width: 70,
            render: (text, record) => {
                var length = record?.quaranting.length;
                var arr = record?.quaranting[length-1];
            return(
            <span size="middle">
                {arr.date_out === null? "គ្មាន":  moment.parseZone(arr?.date_out).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
            </span>
               ) 
            }      
        },
        // moment.parseZone(record?.quaranting?.date_out).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")
        {
            title: 'អាសយដ្ឋាន',
            dataIndex: 'address',
            key: 'address',
            width: 90,
            render: (text, record) => (
                <Space size="middle">
                    {(record?.village !== "ក្រៅសៀមរាប") && record?.village}
                    {record?.commune !== "ក្រៅសៀមរាប" && record?.commune}
                    {record?.district !== "ក្រៅសៀមរាប" && record?.district}
                    {record?.province && record?.province}
                </Space>
            ),
        },
        
        {
            title: 'លក្ខណៈពាក់ព័ន្ធ',
            dataIndex: 'direct',
            key: 'direct',
            width: 50,
            render: (text, record) => (
                <Space size="middle">
                    {record?.direct ? "ផ្ទាល់" : "ប្រយោល"}
                </Space>
            ),
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record?.id}><EyeOutlined /></Link>
                    {/* <span className="link" onClick={() => handleEditSubQuarantine(record)}><EditOutlined /></span> */}
                    {/* <Popconfirm
                        title="តើអ្នកពិតចង់លុបមែនឬទេ?"
                        onConfirm={() => {handleDelete(record?.id) }}
                        okText="ចង់"
                        cancelText="មិនចង់"
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm> */}

                </Space>
            ),
        }
    ]
    return array
}