import React, {useEffect, useState} from 'react'
import { Table, Modal, Row, Col, Button, Select, Divider, Form, Input, DatePicker, message} from 'antd'
import { relatedCol } from '../tableColumn/relatedColumn'
import { ListSelect } from '../../../static/own-comp'
import { convertToCommune, convertToDistrict, convertToVillage, convertPersonalToSelect } from '../../../function/fn'
import { provinceData, districtData, communeData, villageData, genderData, nationalityData } from '../../../context/headerContext'
import { useMutation, useQuery } from '@apollo/client'
import { CREATE_NEW_PERSON, GET_AFFECTEDFROM_BY_PATIENT, GET_ALL_PERSONINFO_NEGATIVE, UPDATE_AFFECTEDFROM } from '../../../graphql/people'
import moment from 'moment'

const {Option} = Select

export default function WorkRelated({open, setOpen, data, setRefetch, fullName, type}) {
    let [form] = Form.useForm()

    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")
    const [personalInfoIdRelated, setPersonalInfoIdRelated] = useState("")

    const {data:personalInfo} = useQuery(GET_ALL_PERSONINFO_NEGATIVE)
    const personalInfoData = personalInfo?.allPersonalInfosForThatNegative
    const [affectedData,setAffectedData] = useState([])

    const { data:affectedD, refetch:refetchAffected } = useQuery(GET_AFFECTEDFROM_BY_PATIENT, {
        variables: {
            page: 1,
            limit: 100,
            keyword: "",
            patientId: data.peopleId
        },
        onCompleted: ({ getAffectedPersonalListWithPagination }) => {

            setAffectedData(getAffectedPersonalListWithPagination)
        },
        fetchPolicy: 'network-only',
        nextFetchPolicy:"cache-only"
    })

    useEffect(()=>{
        if(affectedD!==undefined){
            let affectedFiltered = affectedD?.getAffectedPersonalListWithPagination?.personalInfos?.filter(e => e.affectedFrom.relationType === type)
            setAffectedData(affectedFiltered)

        }
    },[affectedD,open])

    const [createPersonalInfo, { loading: loadingCreate, error: errorCreate }] = useMutation(CREATE_NEW_PERSON, {
        onCompleted: ({ createPersonalInfo }) => {
            // if (personalInfoId === "new") {
            //     if(createPersonalInfo.response.success=== false){
            //         setPeopleSampleFn()
            //         message.error(createPersonalInfo.response.message)
            //     }else{                   
            //         setPersonalInfoId(createPersonalInfo.personalInfo)
            //         message.success("បញ្ចូលទិន្នន័យជោគជ័យ!")
            //     }
            // } else {
                if(createPersonalInfo.response.success=== false){
                    message.error(createPersonalInfo.response.message)
                }else{                   
                    refetchAffected()
                    setRefetch()
                    message.success("បញ្ចូលទិន្នន័យជោគជ័យ!")
                }
                
            // }
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const [updateAffectedFrom, { loading: loadingUpdate, error: errorUpdate }] = useMutation(UPDATE_AFFECTEDFROM, {
        onCompleted: ({ updateAffectedFrom }) => {
            if(updateAffectedFrom.success=== false){
                message.error(updateAffectedFrom.response.message)
            }else{                   
                refetchAffected()
                setRefetch()
                message.success("បញ្ចូលទិន្នន័យជោគជ័យ!")
            }
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const onFinish = (values) => {
        
        if(personalInfoIdRelated === "new"){

            createPersonalInfo({variables: {
                ...values, 
                other: values.other === undefined ? null : values.other, 
                otherAffect: values.otherAffect === undefined ? null : values.otherAffect, 
                age: parseInt(values.age), 
                times: parseInt(values.times),
                village: values.village === undefined ? "ក្រៅសៀមរាប" : values.village, 
                district: values.district === undefined ? "ក្រៅសៀមរាប" : values.district, 
                commune: values.commune === undefined ? "ក្រៅសៀមរាប" : values.commune,
                patientName:fullName,
                patientCode:data.peopleId,
                affectedDate: values.affectedDate === null || values.affectedDate === undefined ? null : moment(values.affectedDate),
                lastTouchAt: values.lastTouchAt === null || values.lastTouchAt === undefined ? null : moment(values.lastTouchAt),
            }})
        } else {
            let index = personalInfoData.findIndex(ele => ele.id === personalInfoIdRelated)
            
            updateAffectedFrom({
                variables:{
                    ...values, 
                    otherAffect: values.otherAffect === undefined ? null : values.otherAffect, 
                    patientCode: data.peopleId,
                    patientName: fullName,
                    personalInfoId: personalInfoData[index].id,
                    affectedDate: values.affectedDate === null || values.affectedDate === undefined ? null : moment(values.affectedDate),
                    lastTouchAt: values.lastTouchAt === null || values.lastTouchAt === undefined ? null : moment(values.lastTouchAt),
                }
            })
        }

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToGenderFn = (e) => {
        form.setFieldsValue({
            gender: e
        });
    };

    const setToPersonalInfoFn = (e) => {
        form.setFieldsValue({
            personalInfoIdRelated: e,
        });

        setPersonalInfoIdRelated(e);
      };

    const setToNationalityFn = (e) => {
        form.setFieldsValue({
            nationality: e
        });
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };


    const handleRelatedDelete =()=>{

    }

    return (
        <Modal
            width={800}
            visible={open}
            title={type ==="ពលករ" ? "តើអ្នកធ្វើដំណើរជាមួយអ្នកណា?":"តើអ្នកបានប៉ះពាល់ជាមួយអ្នកណាខ្លះ នៅកន្លែងធ្វើការរបស់អ្នក?"}
            onCancel={() => setOpen(false)}
            footer={null}
            
        >
            <Form
                form={form}
                name="addAffected"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                fields={[
                    {
                        name:"relationType",value:type
                    },
                    {
                        name:"direct",value:true
                    }
                ]}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{ span: 24, offset: 0 }}
                    >
                        <Form.Item
                        name="personalInfoIdRelated"
                        rules={[{ required: true, message: "ត្រូវបំពេញប្រអប់ខាងលើ!" }]}
                        >
                        <ListSelect
                            type={6}
                            data={convertPersonalToSelect(personalInfoData)}
                            title="ឈ្មោះអ្នកត្រូវយកសំណាក"
                            setValue={setToPersonalInfoFn}
                        />
                        </Form.Item>
                    </Col>

                    {personalInfoIdRelated === "new" ? (
                        <>
                        <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="idCard"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="អត្តសញ្ញាណប័ណ្ណ" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="patientId"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="អត្តលេខអ្នកជំងឺ" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="lastName"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="គោត្តនាម" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="firstName"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="នាម" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="englishName"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="ឈ្មោះជាភាសាឡាតាំង" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="dob"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder="ថ្ងៃខែឆ្នាំកំណើត"/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="gender"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >

                            <ListSelect type={0} data={genderData} title="ភេទ" setValue={setToGenderFn} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="age"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="អាយុ" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="occupation"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="មុខរបរ" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="tel"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="ទូរស័ព្ទ" addonBefore="+855" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="nationality"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <ListSelect type={0} data={nationalityData} title="សញ្ជាតិ" setValue={setToNationalityFn} />
                        </Form.Item>
                    </Col>



                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="ខេត្ត/ក្រុង" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "សៀមរាប" ? (
                        <>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="ស្រុក/ខណ្ឌ" setValue={setToDistrictFn} disabled={province !== "សៀមរាប" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="ឃុំ/សង្កាត់" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 24 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="ភូមិ" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    {/* <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="ត្រូវជា" />
                        </Form.Item>
                    </Col> */}

                    {/* <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vaccinated"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input type="number" placeholder="ចំនួនចាក់វ៉ាក់សាំង" />
                        </Form.Item>
                    </Col> */}

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="chronic"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="ជំងឺប្រចាំកាយ" />
                        </Form.Item>
                    </Col>
                    
                    <Divider>វ៉ាក់សាំង</Divider>
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder="កាលបរិចេ្ឆទវ៉ាក់សាំង"/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="times"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input type="number" placeholder="ចំនួនលើក" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="vaccineType"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Select placeholder="ប្រភេទវ៉ាក់សាំង">
                                <Option value="SINOPHARM">SINOPHARM</Option>
                                <Option value="SINOVAC">SINOVAC</Option>
                                <Option value="ASTRAZENECA">ASTRAZENECA</Option>
                                <Option value="JOHNSON JOHNSON">JOHNSON JOHNSON</Option>
                                <Option value="PFIZER">PFIZER</Option>
                                <Option value="Other">Other</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vacinatedAt"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="ទីកន្លែងទទួលវ៉ាក់សាំង" />
                        </Form.Item>
                    </Col>
                        </>
                    ):null}
                    

                    <Divider>ការពាក់ព័ន្ធ</Divider>
                   
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="direct"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Select placeholder="កម្រិតហានិភ័យ">
                                <Option value={true}>ខ្ពស់</Option>
                                <Option value={false}>ទាប</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="relationType"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Select placeholder="ប្រភេទ" disabled>
                                    <Option value="គ្រួសារ">គ្រួសារ</Option>
                                    <Option value="ពលករ">ពលករ</Option>
                                    <Option value="អ្នករួមការងារ">អ្នករួមការងារ</Option>
                                    <Option value="អ្នករួមចត្តាឡីស័ក">អ្នករួមចត្តាឡីស័ក</Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="ត្រូវជា" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="affectedDate"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <DatePicker style={{width:"100%"}} placeholder="កាលបរិច្ឆេទពាក់ព័ន្ធ" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="lastTouchAt"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <DatePicker style={{width:"100%"}} placeholder="កាលបរិច្ឆេទពាក់ព័ន្ធចុងក្រោយ" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="otherAffect"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input.TextArea placeholder="ចំណាំ" />
                        </Form.Item>
                    </Col>
                    {/* <Divider>ពាក់ព័ន្ធករណី</Divider>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="case"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >

                            <ListSelect type={4} data={allCases} title="ករណី" setValue={setToCaseFn} disabled={caseId === "new" ? true : false} />
                        </Form.Item>
                    </Col>

                    {
                        caseId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="caseName"
                                >
                                    <Input disabled={true} style={{ backgroundColor: "white", color: "black" }} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }

                    <Col xs={24} md={caseId === "new" ? { span: 24 } : { span: 11, offset: 2 }}>
                        <Form.Item
                            name="direct"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Select placeholder="លក្ខណៈពាក់ព័ន្ធ" style={{ width: "100%" }}>
                                <Option value={true}>ផ្ទាល់</Option>
                                <Option value={false}>ប្រយោល</Option>

                            </Select>
                        </Form.Item>
                    </Col>

                    

                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="other"

                        >
                            <Input placeholder="ចំណាំ" />
                        </Form.Item>
                    </Col> */}


                    {/* <Col xs={24}>
                        <Form.Item
                            name="interviewed"
                            valuePropName="checked"
                        >
                            <Checkbox>សម្ភាសរួចរាល់</Checkbox>
                        </Form.Item>
                    </Col> */}


                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ 
                                width: "100%",
                            }}
                        >
                            បញ្ចូល
                        </Button>
                    </Col>
                    <Col
                        xs={24}
                        style={{
                            marginTop: 20
                        }}
                    >
                        <Table
                            className="table-personal"
                            columns={relatedCol({ handleRelatedDelete })}
                            dataSource={affectedData}
                            rowKey={record => record.id}
                            pagination={false}
                            // pagination={{
                            //     total: subCasePagination?.totalDocs,
                            //     // showSizeChanger: true,
                            //     onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                            // }}
                            scroll={{ x: 500, y: 300 }}
                            sticky
                        />
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
